<template>
  <view class="test-table">
    <view class="header">
      <view class="header-title">
        {{ examTitle }}

      </view>

      <view class="header-process">
        {{ currentQuestion.index + 1 }}/{{ list.length }}
      </view>

    </view>

    <view class="question">
      {{ currentQuestion.index + 1 }}、{{ currentQuestion.title }}
    </view>

    <view class="answer">
      <view 
        v-for="(item,index) in currentQuestion.answerList" 
        :key="index" 
        class="answer-item">
        <u-button 
          :custom-style="currentQuestion.selectAnswer === item.itemId ? activeStyle : ''" 
          size="default"
          @click="changeSelect(item)">{{ item.label }}</u-button>

      </view>

    </view>
    <view class="footer">

      <u-button 
        :disabled="currentQuestion.index === 0" 
        class="footer-button" 
        shape="circle"
        @click="backQuestion">上一题</u-button>

      <u-button 
        :disabled="currentQuestion.index === list.length - 1" 
        class="footer-button" 
        type="primary" 
        shape="circle"
        @click="nextQuestion">
        下一题</u-button>

    </view>

    <u-toast ref="uToast" />

  </view>
</template>

<script>
export default {
  data() {
    return {
		
      examId: '',
      userForm: '',
	  
	  examTitle: '',

      currentQuestion: {

        index: 0,

        title: '',

        selectAnswer: '',
        answerList: []
      },

      list: [],

      activeStyle: {
        backgroundColor: '#a0cfff',
        color: '#FFFFFF'
      }
    }
  },
  onLoad(options) {
	  this.userForm = JSON.parse(decodeURIComponent(options.form))
	  
	  this.examId = options.id
	  this.$u.api.getTestTable({
		  examId: options.id
	  }).then(res => {
		  this.list = res
		  
		  let {title, answerList, examTitle} = res[0]
		  this.examTitle = examTitle
		  this.currentQuestion = {
			  index: 0,
			  title,
			  selectAnswer: '',
			  answerList
		  }
		  })
  },
  methods: {
    changeSelect(answerItem) {
      let index = this.currentQuestion.index
      if (index === this.list.length - 1) {
		  let degree = 0
		  let score = 0
		  let submitList = []
		  let analyse = ''
		  this.list[index].selectAnswer = answerItem.itemId
		  
		  for (let item of this.list) {
			 item.answerList.forEach(each => {
				 if (each.itemId === item.selectAnswer) {
					 console.log(each)
					 score = each.value + score
					 
					 submitList.push({
						 questionId: item.id,
						 itemId: answerItem.itemId
					 })
				 }
			 })
		  }
		  
		  switch (this.examId) {
			  case '3':
			  if (score > 30) {
				  analyse = '睡眠质量差，极端时可能导致惊恐发作。 莫名担心、害怕的感觉，伴有心慌、心跳加快，易紧张、多汗，有掩饰的举动。'
				  degree = 2
			  } else if (score < 20) {
				    analyse = '睡眠质量比较安定。'
            degree = 0
			  } else {
				  analyse = '出现睡眠质量问题。'
				  degree = 1
			  }
			  break
			  case '1':
			  if (score > 30) {
			  				  analyse = '倔强症状明显，表现为担心、害怕而反复思考某些念头和想法，如洗手或一些特殊的动作。'
							  	degree = 2
			  } else if (score < 20) {
			  				    analyse = '倔强症状不明显。'
            degree = 0
			  } else {
			  				  analyse = '出现倔强症状。'
							  	degree = 1
			  }
			  break
			  case '2':
			  if (score > 39) {
			  				  analyse = '情绪浮动明显，有死亡的思想和自杀的念头。自我评价低，心情欠佳，兴趣下降，偶有烦躁，不愿意与人交往，时有睡眠差、多梦。'
							  	degree = 2
			  } else if (score < 26) {
			  				    analyse = '情绪浮动不明显，生活态度积极乐观。'
            degree = 0
			  } else {
			  				  analyse = '出现情绪浮动。'
							  	degree = 1
			  }
			  break
		  }
		  
		  this.$u.api.createTest({
			  ...this.userForm,
          analyse,
          score,
          list: JSON.stringify(submitList),
          degree,  
		  examId: this.examId
		  }).then(res => {
			  uni.redirectTo({
			    url: `../test-result/test-result?examTitle=${this.examTitle}&&analyse=${analyse}`
			  })
		  })
      } else {
        this.list[index].selectAnswer = answerItem.itemId

        this.currentQuestion = this.list[index + 1]

        this.currentQuestion.index = index + 1
      }
    },
    backQuestion() {
      let index = this.currentQuestion.index

      this.currentQuestion = this.list[index - 1]

      this.currentQuestion.index = index - 1
    },
    nextQuestion() {
      let index = this.currentQuestion.index

      if (!this.currentQuestion.selectAnswer) {
        this.$refs.uToast.show({
          title: '请完成选项',
          icon: false
        })
      } else {
        if (index === this.list.length - 1) {
          uni.navigateTo({
            url: '../test-result/test-result'
          })
        } else {
          this.currentQuestion = this.list[this.currentQuestion.index + 1]
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
	.test-table {

		.header {

			display: flex;
			justify-content: space-between;
			align-items: center;
			margin: 20rpx;

			&-title {
				font-size: $uni-font-size-lg;

			}

			&-process {
				color: #909399;
			}
		}

		.question {
			margin: 20rpx;
		}

		.answer {
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			margin: 100rpx 20rpx;

			&-item {
				margin: 40rpx 0;
				width: 500rpx;
			
			}

		}

		.footer {
			position: fixed;
			bottom: 0;
			width: 100%;
			box-shadow: 0 -6rpx 6rpx rgba(0, 0, 0, 0.1);
			display: flex;
			justify-content: center;
			align-items: center;
			height: 120rpx;

			justify-content: space-around;
			align-items: center;

			&-button {
				width: 320rpx;
			}
		}

	}
</style>
